@keyframes backgroundChange {
  0% {
    background-color: #ffff00; }
  50% {
    background-color: #00ff00; }
  100% {
    background-color: #ffff00; } }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-direction: column; }

header {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 100px; }

.navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  width: 100%; }
  .navigation__up {
    text-transform: uppercase;
    font-family: "Kanit", sans-serif;
    border: 1px solid #000000;
    border-radius: 25px;
    background: linear-gradient(to right, #ffff00, #00ff00);
    position: fixed;
    bottom: 100px;
    right: 80px; }
  .navigation__searchTool {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    font-family: "Lobster", cursive; }
  .navigation__input {
    width: 250px;
    border-radius: 5px;
    margin-right: 10px;
    border: 1px solid #000000; }
  .navigation__title {
    font-size: 60px;
    font-family: "Kanit", sans-serif;
    text-transform: uppercase;
    letter-spacing: 5px;
    background: linear-gradient(to right, #ffff00, #00ff00);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-color: #000000;
    -webkit-text-stroke-width: 1px; }

main {
  width: 100%; }

.formWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 100px; }

.addForm {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0; }
  .addForm__inputsWrapper {
    width: 459.5px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px; }
  .addForm__title {
    border-radius: 5px;
    border: 1px solid #000000;
    font-family: "Lobster", cursive; }
  .addForm__submitBtn {
    border-radius: 5px;
    border: 1px solid #000000;
    font-family: "Kanit", sans-serif;
    text-transform: uppercase;
    background: linear-gradient(to right, #ffff00, #00ff00);
    transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out; }
    .addForm__submitBtn:hover {
      transform: scale(1.15); }
    .addForm__submitBtn:active {
      background: #ffffff; }
  .addForm__content {
    margin: 0;
    padding: 0;
    animation: backgroundChange 10s ease-in-out infinite;
    border-radius: 5px;
    border: 1px solid #000000;
    font-family: "Lobster", cursive; }

.todo {
  width: 100%;
  padding: 0; }
  .todo__item {
    list-style: none;
    padding: 0 0 50px 0; }
  .todo__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px; }
  .todo__index {
    box-sizing: border-box;
    font-family: "Kanit", sans-serif;
    border: 1px solid #000000;
    border-radius: 25px;
    background-color: #ffff00;
    height: 50px;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 24px;
    transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out; }
    .todo__index:hover {
      background-color: #00ff00;
      transform: scale(1.15); }
  .todo__title {
    font-family: "Lobster", cursive;
    font-weight: bold; }
  .todo__deleteBtn {
    height: 50px;
    width: 50px;
    font-family: "Kanit", sans-serif;
    border: 1px solid #000000;
    border-radius: 25px;
    background-color: #00ff00;
    transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out; }
    .todo__deleteBtn:hover {
      background-color: #ffff00;
      transform: scale(1.15); }
  .todo__content {
    margin: 0 15px;
    font-family: "Lobster", cursive; }
  .todo__date {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
